@import "../utilities/Variables";
ul {
  margin: 0;
  padding: 0;
}
input,
button {
  background-image: none;
  background-color: transparent;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
  outline: none;
  border: none;
}
a {
  text-decoration: none;
  color: $bitterLime;
}
b {
  font-weight: 400;
  color: $darkerSoftCyan;
}
